ul.socialmedia {
    position: relative;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    margin-bottom: 1rem;
}

ul.socialmedia a {
    display: block;
    margin-right: 1rem;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: #eee;
    border-radius: 100%;
    /*border: 3px solid transparent;*/
}

ul.socialmedia a:hover {
    background-color: white;
}

ul.socialmedia .twitter {
    background-image: url(../assets/images/twitter.svg);
}

ul.socialmedia .opensea {
    background-image: url(../assets/images/opensea.svg);
}

ul.socialmedia .arbitrum {
    background-image: url(../assets/images/arbitrum.png);
}